import React, { useState } from "react";
import "./header.css";
import { withPublicUrl } from "../utils/assetPath";
import { NavLink, useNavigate } from "react-router-dom";
import { FiClipboard, FiHeart, FiLogOut, FiSearch, FiUser } from "react-icons/fi";
import { HiOutlineMenu } from "react-icons/hi";
import { useCart } from "../context/CartContext";
import { useAuth } from "../context/AuthContext";
import CartDrawer from "./CartDrawer";

export default function Header() {
  const [menuOpen, setMenuOpen] = useState(false);
  const [clothsOpen, setClothsOpen] = useState(false);
  const [jewelleryOpen, setJewelleryOpen] = useState(false);
  const [searchOpen, setSearchOpen] = useState(false);
  const [searchText, setSearchText] = useState("");
  const [cartOpen, setCartOpen] = useState(false);
  const [logoutOpen, setLogoutOpen] = useState(false);

  //  CORRECT: get unique product count
  const { uniqueItemCount } = useCart();
  const { logout } = useAuth();

  const navigate = useNavigate();

  const getDisplayName = () => {
    try {
      const storedUser = localStorage.getItem("user");
      if (!storedUser) return "";
      const parsed = JSON.parse(storedUser);
      const raw = parsed?.name || parsed?.firstName || parsed?.id || "";
      if (!raw) return "";
      if (raw.includes("@")) {
        const localPart = raw.split("@")[0] || "";
        const cleaned = localPart.replace(/\d+/g, "");
        const surname = cleaned.toLowerCase().startsWith("patel")
          ? cleaned.slice(5)
          : cleaned;
        if (!surname) return "";
        return surname.charAt(0).toUpperCase() + surname.slice(1);
      }
      return raw
        .replace(/[._-]+/g, " ")
        .replace(/\s+/g, " ")
        .trim()
        .split(" ")
        .map((word) =>
          word ? word.charAt(0).toUpperCase() + word.slice(1) : ""
        )
        .join(" ");
    } catch (err) {
      return "";
    }
  };

  const displayName = getDisplayName();
  const isLoggedIn = Boolean(displayName);

  /* =========================
     SEARCH SUBMIT
  ========================= */
  const handleSearchSubmit = (e) => {
    e.preventDefault();

    const query = searchText.trim();
    if (!query) return;

    navigate(`/search?q=${encodeURIComponent(query)}`);
    setSearchOpen(false);
    setSearchText("");
  };

  return (
    <>
      {/* TOP BAR */}
      <div className="top-bar">
        {isLoggedIn && (
          <div className="top-left">Welcome, {displayName}</div>
        )}

        <div className="top-text">
          <b>Free Delivery</b> on all orders over <b>1999</b> !!
        </div>

        <div className="top-right">
          <button
            type="button"
            className="icon-btn tooltip"
            data-tooltip="Search"
            onClick={() => setSearchOpen(true)}
            aria-label="Search"
          >
            <FiSearch />
          </button>

          {isLoggedIn ? (
            <>
              <NavLink
                to="/wishlist"
                className="top-link icon-link tooltip"
                aria-label="WishList"
                data-tooltip="Wishlist"
              >
                <FiHeart />
              </NavLink>
              <NavLink
                to="/order-history"
                className="top-link icon-link tooltip"
                aria-label="Order History"
                data-tooltip="Order History"
              >
                <FiClipboard />
              </NavLink>
              <NavLink
                to="/user-profile"
                className="top-link icon-link tooltip"
                aria-label="User Profile"
                data-tooltip="User Profile"
              >
                <FiUser />
              </NavLink>
              <button
                type="button"
                className="top-link icon-link logout-btn tooltip"
                onClick={() => {
                  setLogoutOpen(true);
                }}
                aria-label="Logout"
                data-tooltip="Logout"
              >
                <FiLogOut />
              </button>
            </>
          ) : (
            <button
              type="button"
              className="icon-btn tooltip"
              data-tooltip="Login"
              onClick={() => navigate("/login")}
              aria-label="Login"
            >
              <FiUser />
            </button>
          )}
        </div>
      </div>

      {/* SEARCH */}
      <form
        className={`search-bar ${searchOpen ? "active" : "hidden"}`}
        onSubmit={handleSearchSubmit}
      >
        <input
          type="text"
          placeholder="Search products..."
          value={searchText}
          onChange={(e) => setSearchText(e.target.value)}
          autoFocus
        />
        <button
          type="button"
          className="search-close"
          onClick={() => setSearchOpen(false)}
        >
          
        </button>
      </form>

      {/* NAVBAR */}
      <nav className="navbar">
        <div className="logo">
          <NavLink to="/">
            <img src={withPublicUrl("images/headerlogo.jpeg")} alt="Aashaka" />
          </NavLink>
        </div>

        {/* DESKTOP MENU */}
        <ul className="nav-links desktop">
          <li><NavLink to="/">HOME</NavLink></li>

          <li className="dropdown">
            <span>CLOTHS</span>
            <div className="dropdown-menu">
              <NavLink to="/kurti">Kurti Set</NavLink>
            </div>
          </li>

          <li className="dropdown">
            <span>JEWELLERY</span>
            <div className="dropdown-menu">
              <NavLink to="/jewellery/oxidised">Oxidised Set</NavLink>
              <NavLink to="/jewellery/bangles">Bangles-Kada</NavLink>
              <NavLink to="/jewellery/earrings">Earrings</NavLink>
              <NavLink to="/jewellery/necklace">Necklace</NavLink>
            </div>
          </li>

          <li><NavLink to="/about">ABOUT</NavLink></li>

          <li>
            <button
              className="cart-link"
              onClick={() => setCartOpen(true)}
            >
              CART ({uniqueItemCount})
            </button>
          </li>
        </ul>

        {/* MOBILE RIGHT */}
        <div className="nav-right">
          <span
            className="cart-text"
            onClick={() => setCartOpen(true)}
          >
            CART ({uniqueItemCount})
          </span>

          <button
            className="mobile-search"
            onClick={() => setSearchOpen(true)}
            aria-label="Open search"
          >
            <FiSearch />
          </button>

          <button
            className="hamburger"
            onClick={() => setMenuOpen(!menuOpen)}
            aria-label="Open menu"
          >
            <HiOutlineMenu />
          </button>
        </div>
      </nav>

      {/* MOBILE MENU */}
      <div className={`mobile-menu ${menuOpen ? "open" : ""}`}>
        <NavLink to="/" onClick={() => setMenuOpen(false)}>HOME</NavLink>
        <NavLink to="/about" onClick={() => setMenuOpen(false)}>ABOUT</NavLink>

        <div className="mobile-accordion">
          <button
            className={`mobile-title ${clothsOpen ? "active" : ""}`}
            onClick={() => setClothsOpen(!clothsOpen)}
          >
            CLOTHS
          </button>
          <div className={`mobile-submenu ${clothsOpen ? "open" : ""}`}>
            <NavLink to="/kurti" onClick={() => setMenuOpen(false)}>
              Kurti Set
            </NavLink>
          </div>
        </div>

        <div className="mobile-accordion">
          <button
            className={`mobile-title ${jewelleryOpen ? "active" : ""}`}
            onClick={() => setJewelleryOpen(!jewelleryOpen)}
          >
            JEWELLERY
          </button>
          <div className={`mobile-submenu ${jewelleryOpen ? "open" : ""}`}>
            <NavLink to="/jewellery/oxidised" onClick={() => setMenuOpen(false)}>
              Oxidised set
            </NavLink>
            <NavLink to="/jewellery/bangles" onClick={() => setMenuOpen(false)}>
              Bangles-Kada
            </NavLink>
            <NavLink to="/jewellery/earrings" onClick={() => setMenuOpen(false)}>
              Earrings
            </NavLink>
            <NavLink to="/jewellery/necklace" onClick={() => setMenuOpen(false)}>
              Necklace
            </NavLink>
          </div>
        </div>

        {isLoggedIn ? (
          <>
            <NavLink to="/wishlist" className="account-link" onClick={() => setMenuOpen(false)}>
              WISHLIST
            </NavLink>
            <NavLink to="/order-history" className="account-link" onClick={() => setMenuOpen(false)}>
              ORDER HISTORY
            </NavLink>
            <NavLink to="/user-profile" className="account-link" onClick={() => setMenuOpen(false)}>
              USER PROFILE
            </NavLink>
            <button
              type="button"
              className="account-link mobile-logout"
              onClick={() => {
                setLogoutOpen(true);
              }}
            >
              LOGOUT
            </button>
          </>
        ) : (
          <NavLink to="/login" onClick={() => setMenuOpen(false)}>
            LOGIN / REGISTRATION
          </NavLink>
        )}
      </div>

      <CartDrawer open={cartOpen} onClose={() => setCartOpen(false)} />

      {logoutOpen && (
        <div
          className="logout-modal-overlay"
          role="dialog"
          aria-modal="true"
          aria-labelledby="logout-title"
        >
          <div className="logout-modal">
            <h3 id="logout-title">Log out?</h3>
            <p>Are you sure you want to log out of Aashaka?</p>
            <div className="logout-actions">
              <button
                type="button"
                className="logout-btn-secondary"
                onClick={() => setLogoutOpen(false)}
              >
                Cancel
              </button>
              <button
                type="button"
                className="logout-btn-primary"
                onClick={() => {
                  logout();
                  setLogoutOpen(false);
                  setMenuOpen(false);
                  navigate("/login");
                }}
              >
                Log out
              </button>
            </div>
          </div>
        </div>
      )}
    </>
  );
}

